import React from "react";
import ReactDOM from 'react-dom/client';
import 'bootstrap/dist/css/bootstrap.min.css'
import { Button, Container, Row, Col, Nav, NavDropdown, Navbar, NavbarBrand, Badge } from "react-bootstrap";
import logo from "../logo.png";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook } from '@fortawesome/free-brands-svg-icons'
import { faEnvelope } from '@fortawesome/free-solid-svg-icons'
import { faGithub } from '@fortawesome/free-brands-svg-icons'
import { faLinkedin } from '@fortawesome/free-brands-svg-icons'

const NavBar = () => {
    return <>
        <header>
            <Navbar expand="lg" className="navbar">
                <Container className="navContainer">
                    <Navbar.Brand href="#home"><img className="logo animate__animated animate__jackInTheBox" src={logo} alt="My Logo" /></Navbar.Brand>
                    <Navbar.Toggle aria-controls="standard-navbar-nav" />
                    <Navbar.Collapse id="standard-navbar-nav">
                        <Nav className=" custom-nav justify-content-end flex-grow-1 pe-3">
                            <Nav.Link href="#home">Home</Nav.Link>
                            <Nav.Link href="#about">About</Nav.Link>
                            <Nav.Link href="#skills">Skills</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
            <div className="contacts" >
                <a href="https://www.facebook.com/cjbaqui.baquiran/">
                    <FontAwesomeIcon className="animateFb animate__animated animate__backInLeft animate__faster" icon={faFacebook} />
                </a>
                <a href="https://github.com/CJ-BAQUI">
                    <FontAwesomeIcon className="animateGh animate__animated animate__backInLeft animate__delay-1s animate__faster" icon={faGithub} />
                </a>
                <a href="mailto:crisjohn.baquiran@softvision.com">
                    <FontAwesomeIcon className="animateEn animate__animated animate__backInLeft animate__delay-2s animate__faster" icon={faEnvelope} />
                </a>
                <a href="https://www.linkedin.com/public-profile/settings?trk=d_flagship3_profile_self_view_public_profile">
                    <FontAwesomeIcon className="animateLi animate__animated animate__backInLeft animate__delay-3s animate__faster" icon={faLinkedin} />
                </a>

            </div>
        </header>
    </>
}

export default NavBar;