import React from "react";
import ReactDOM from 'react-dom/client';
import 'bootstrap/dist/css/bootstrap.min.css'
import { Button, Container, Row, Col, Nav, NavDropdown, Navbar, NavbarBrand, Card } from "react-bootstrap";
import Typewriter from "typewriter-effect";
import myPic from "../myImage.png";


const Home = () => {
    return <div className="home fluid">
        <Container className="homeContainer" fluid>
            <Row>
                <Col className="homeRow" lg={6} md={5} sm={12} xs={12}>
                    <div className="welcome" id="home">
                        <div className="welcomeText">
                            <Typewriter className="p-2 flex-shrink-2" loop={true}
                                onInit={(typewriter) => {
                                    typewriter
                                        .typeString("Welcome!")
                                        .pauseFor(1000)
                                        .deleteAll()
                                        .typeString("I'm Baqui")
                                        .pauseFor(1000)
                                        .deleteAll()
                                        .typeString("I'm a Jr. Software Engineer")
                                        .pauseFor(1000)
                                        .deleteAll()
                                        .typeString("I do Backend")
                                        .pauseFor(1000)
                                        .deleteAll()
                                        .typeString("Frontend")
                                        .pauseFor(1000)
                                        .deleteAll()
                                        .typeString("and Database")
                                        .pauseFor(1000)
                                        .deleteAll()
                                        .typeString("Welcome!")
                                        .start();
                                }}
                            />
                        </div>
                    </div>
                </Col>
                <Col className="homeRow" lg={6} md={7} sm={12} xs={12}>
                    <Card className="card mx-auto animate__animated animate__flipInX">
                        <img className="myPic" src={myPic} alt="My Picture" />
                        <div className="nameTag">
                            <h3>Cris John Baquiran</h3>
                            <h4>Jr. Software Engineer</h4>
                        </div>
                        <Button className="btn d-flex justify-content-center" href="#">Projects</Button>
                    </Card>
                </Col>
            </Row>
        </Container>
    </div>
}

const Name = () => {
    return <h1>Cris John Baquiran</h1>
}

export default Home;