import React, {useRef} from "react";
import { Container, Row, Col, Accordion } from "react-bootstrap";
import emailjs from '@emailjs/browser';

const Footer = () => {
    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_6xn6qgd', 'template_f39nd37', form.current, '3NkDn0TuhyrDpM2ev')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
    };

    return <>
        <Container className="footer" fluid>
            <div>
                <Row>
                    <Col className="footerContent position-absolute justify-self-end" >
                        <div className="directories d-flex justify-content-center">
                            <a href="#home">Home</a>
                            <a href="#about">About</a>
                            <a href="#skills">Skills</a>
                        </div>
                        <div className="copyright d-flex justify-content-center">
                            <h6>Copyright by</h6>
                            <h1>Cris John Baquiran</h1>
                            <div className="line"></div>
                            <h2>2023</h2>
                        </div>
                    </Col>
                </Row>
            </div>
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="1">
                    <Accordion.Header>Connect with Me!</Accordion.Header>
                    <Accordion.Body>
                        <div className="contactForm">
                            <form ref={form} onSubmit={sendEmail}>
                                <Row>
                                    <Col>
                                        <label>Name: <input type="text" className="name" name="name" /></label>
                                        <label>Email: <input type="email" className="email" name="email" /></label>
                                        <label>Contact No.: <input type="number" className="contact" name="contact" /></label>
                                    </Col>
                                    <Col>
                                        <label>Message: <textarea className="message" name="message" /></label>
                                        <input type="submit" className="submit" value="Send" />
                                    </Col>
                                </Row>

                                                                    
                            </form>
                        </div>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>;
        </Container>

    </>
}

export default Footer;