import React from "react";
import ReactDOM from 'react-dom/client';
import 'bootstrap/dist/css/bootstrap.min.css'
import { Button, Container, ProgressBar} from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faJava, faSquareJs } from '@fortawesome/free-brands-svg-icons';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import Java from "../skillsBadge/java.png"
import JS from "../skillsBadge/js.png"
import Css from "../skillsBadge/css.png"
import ReactIcon from "../skillsBadge/react.png"
import Bootstrap from "../skillsBadge/bootstrap.png"
import SP from "../skillsBadge/spring-boot.png"

const Skills = () => {
    const responsive = {
        superLargeDesktop: {
            // the naming can be any, depends on you.
            breakpoint: { max: 4000, min: 3000 },
            items: 5
        },
        desktop: {
            breakpoint: { max: 3000, min: 1024 },
            items: 3
        },
        tablet: {
            breakpoint: { max: 1024, min: 464 },
            items: 2
        },
        mobile: {
            breakpoint: { max: 464, min: 0 },
            items: 1
        }
    };

    return <>
        <Container fluid id="skills" className="skills">
            <div className="skill-bx">
                <h2>Skills</h2>
                <Carousel className="carousel" responsive={responsive} infinite={true} swipeable={true} 
                keyBoardControl={true} removeArrowOnDeviceType={["tablet", "mobile"]}>
                    <div className="item">
                        <img src={Java} alt="Java" />
                        <ProgressBar now={85} label={85}/>
                        <h4>Java</h4>
                    </div>
                    <div className="item">
                        <img src={JS} alt="Javascript" />
                        <ProgressBar now={70} label={70}/>
                        <h4>Javascript</h4>
                    </div>
                    <div className="item">
                        <img src={Css} alt="CSS" />
                        <ProgressBar now={70} label={70}/>
                        <h4>CSS</h4>
                    </div>
                    <div className="item">
                        <img src={ReactIcon} alt="React" />
                        <ProgressBar now={80} label={80}/>
                        <h4>React</h4>
                    </div>
                    <div className="item">
                        <img src={Bootstrap} alt="Bootstrap" />
                        <ProgressBar now={70} label={70}/>
                        <h4>Bootstrap</h4>
                    </div>
                    <div className="item">
                        <img src={SP} alt="Spring-boot" />
                        <ProgressBar now={87} label={87}/>
                        <h4>Spring boot</h4>
                    </div>
                </Carousel>
            </div>
        </Container>
    </>
}

export default Skills;